}



.form-container {
    min-height: 100vh;
    padding-left: 50px;
    padding-right: 50px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.form__title {
    width: 100%;
    display: block;
    font-family: Poppins-Regular;
    font-size: 30px;
    color: #333;
    line-height: 1.2;
    text-align: center;
    padding-bottom: 43px;
    text-transform: uppercase;
}

form {
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.form__input {
    position: relative;
    width: 100%;
    border: 1px solid black;
    padding: 10px;
    padding-top: 20px;
    border-radius: 10px;
    margin-bottom: 20px;
    min-height: 50px;
}

label {
    width: 100%;
}

span.label-span {
    position: absolute;
    left: 10px;
    transition: all 0.3s ease;
    pointer-events: none;
}

.form__input input:focus+span, .form__input input:valid+span {
    -webkit-transform: translateY(-20px) scale(0.8);
            transform: translateY(-20px) scale(0.8);
    color: rgb(105, 104, 104);
}



.form__button{
    text-align: center;
    width: 40%;
    height: 40px;
    background-color: rgb(124, 94, 39);
    color: white;
    border-radius: 5px;
    -webkit-transform: perspective(1px) translateZ(0);
            transform: perspective(1px) translateZ(0);
    border: none;
    position: relative;
    transition-property: color;
    transition-duration: 0.3s;
}

.form__button::before{
    border-radius: 5px;
    content: "";
    position: absolute;
    z-index: -1;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background: #53c9a5;
    -webkit-transform: scaleX(0);
            transform: scaleX(0);
    -webkit-transform-origin: 0 50%;
            transform-origin: 0 50%;
    transition-property: -webkit-transform;
    transition-property: transform;
    transition-property: transform, -webkit-transform;
    transition-duration: 0.3s;
    transition-timing-function: ease-out;

}

.form__button:hover::before{
    -webkit-transform: scaleX(1);
            transform: scaleX(1);
    border-color: #53c9a5;
    
}

.form__button:hover{
    border-color: turquoise;
}

.change-form{
    margin-top: 50px;
}

.change-form span{
    font-weight: bold;
}

.change-form span:hover{
    cursor: pointer;
    color: rgb(143, 95, 6);
}

@media (max-width: 768px) {
    .form__input{
        width: 60%;
    }

    .form__button{
        margin-top: 50px;
        width: 20%;
    }

    .form-container{
        background-image: url("https://colorlib.com/etc/lf/Login_v18/images/bg-01.jpg");
        background-position: center;
        background-repeat: no-repeat;
        background-size: cover;
        width: 100%;
    }

    .form__input{
        background-color: white;
    }

    .change-form > button{
        background-color: #0a8d48!important;
    }
}
    
