    right: 0px;
    bottom: 15px;
   
}

@-webkit-keyframes rightDown {

    50% {
      bottom: 6px;
      right: -16px;
    }

    100% {
      bottom: -2px;
      right: -24px;
    }
  }

@keyframes rightDown {

    50% {
      bottom: 6px;
      right: -16px;
    }

    100% {
      bottom: -2px;
      right: -24px;
    }
  }

  @-webkit-keyframes drop {
    100% {
      -webkit-transform: translate(120px, calc(35px + (100vh/2)));
              transform: translate(120px, calc(35px + (100vh/2)));
    }
  }

  @keyframes drop {
    100% {
      -webkit-transform: translate(120px, calc(35px + (100vh/2)));
              transform: translate(120px, calc(35px + (100vh/2)));
    }
  }


